.charts {
  display: flex;
  flex-direction: column;
}
.detailsContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 20px;
  background-color: rgba(240, 212, 178, 0.5);
  box-shadow: 0 2px 4px 0 rgba(184, 184, 184, 0.5);
  padding: 30px;
  border-radius: 20px;
}

.moves {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.move {
  border-radius: 5px;
  margin: 5px 5px 5px 0;
  padding: 10px;
}

.ellipse {
  background-image: url("../assets/ellipse.png");
  background-position: bottom;
  background-size: 50%;
  background-repeat: no-repeat;
  margin: "0 100px";
}

@media (max-width: 767px) {
  .charts {
    display: flex;
    flex-direction: column;
  }
  .moves {
    display: flex;
    max-width: 300px;
    flex-direction: row;
    flex-wrap: wrap;
  }


  .detailsContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  
  }
}
