.card {
  height: 100px;
  text-align: center;
  padding: 2rem;
  color: blue;
  width: 25%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 2rem auto;
  overflow-y: hidden;
  border-radius: 20px;
  box-shadow: 0 2px 4px 0 rgba(184, 184, 184, 0.5);
  transition: box-shadow linear 400ms;
}
.types{
  display: flex;
  flex-direction: row;
}
.pic{
  background-image: url('../assets/pequena.png');
  background-position: right;
}
.card:hover {
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.4), inset 0 -1px 0 0 #dadce0;
  cursor: pointer;
}

@media (max-width: 767px) {
  .card {
    height: auto;
    text-align: center;
    padding: 2rem;
    color: blue;
    width: 70%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-around;
    
    margin: 2rem 20px;
    overflow-y: hidden;
    border-radius: 20px;
    box-shadow: 0 2px 4px 0 rgba(184, 184, 184, 0.5);
    transition: box-shadow linear 400ms;
  }
}
