.list {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  width: 80%;
  flex-wrap: wrap;
}
.search {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.boton {
  height: 30px;
  width: 100px;
  background-color: #f87f0c;
  border-radius: 15px;
  overflow: hidden;
  text-align: center;
  opacity: 0.6;
  font-size: 20px;
  margin-left: 10px;
  padding: 5px;
  transition: color 1s;
}
.boton:hover {
  opacity: 0.2;
  cursor: pointer;
  color: white;
}
input {
  outline: none;
  font-family: inherit;
  margin: 0 auto;
  border: #ae9935;
  opacity: 0.6;
  font-size: 20px;
  width: 400px;
  color: #ae9935;
  padding: 7px 10px;
  border-radius: 15px;
  background: #f0d44e;
}
.listContainer {
}
.paginar {
  width: 100%;
  background-color: #f0d44e;
  height: auto;
  font-weight: 700;
  font-size: 28px;
  color: white;
  text-align: center;
  padding: 10px;
  cursor: pointer;
  margin-bottom: 20px;
}

