@media (min-width: 768px) and (max-width: 1024px) {
}

@media (max-width: 767px) {
  input {
 
    width: 90%;
   
  }
  .search {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: flex-start;
    padding: 20px;
    margin: 20px;
  }

  .boton {
    height: 30px;
    width: 20%;
    background-color: #f87f0c;
    border-radius: 15px;
    overflow: hidden;
    text-align: center;
    opacity: 0.6;
    font-size: 20px;
    margin: 20px 0 0 0;

  }
  .list {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    flex-wrap: nowrap;
  }

}

