.container {
  background-color: #f0d44e;
  height: 340px;
  opacity: 0.9;

  margin: 0;
  width: "100%";
  color: #ae9935;
  padding: 0;
  padding: 10px 0 10px 10px;
  display: grid;
  grid-template-columns: repeat(1fr, 16);
  grid-template-rows: repeat(1fr, 16);
}
.title {
  font-size: 120px;
  opacity: 1 !important;
  grid-area: 1/1/3/3;
}
.bars {
  grid-area: 1/5/14/17;
  padding: 5px 0 5px 5px;
  background-image: url("../assets/bars.png");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
}



.rueda {
  grid-area: 1/1/12/3;
  background-image: url("../assets/rueda.png");
  background-position: center;
  background-size: 40%;
  background-repeat: no-repeat;
}

