/* 
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {
  .container {
    background-color: #f0d44e;
    height: 340px;
    opacity: 0.9;
    margin: 0;
    width: "100%";
    color: #ae9935;
    padding: 0;
    padding: 10px 0 10px 10px;
    display: grid;
    grid-template-columns: repeat(1fr, 16);
    grid-template-rows: repeat(1fr, 16);
  }
}

