@media (max-width: 767px) and (orientation: landscape) {
  .container {
    display: flex;
    flex-direction: column;
    height: 180px;
    align-content: center;
  }
  .title {
    font-size: 60px;
    opacity: 1 !important;
    grid-area: 1/1/7/3;
  }
  .rueda {
    background-image: url("../assets/rueda.png");
    background-position: center;
    background-size: 40%;
    background-repeat: no-repeat;
  }
}

/* 
    ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px
  */

@media (min-width: 320px) and (max-width: 480px) {
  .container {
    display: flex;
    flex-direction: column;
    height: 180px;
    text-align: center;
  }
  .title {
    font-size: 60px;
    opacity: 1 !important;
    grid-area: 1/1/7/3;
  }
  .rueda {
    position: absolute;
    top: 10px;
    background-image: url("../assets/rueda.png");
    background-position: center;
    background-size: 40%;
    background-repeat: no-repeat;
  }

  .bars {
    grid-area: 1/3/7/5;
    padding: 15px;
    background-image: none;
  }
}
